import { Loading } from '@miq/componentjs';
import { SharedDataCtx, TSDState } from '@miq/contextjs';
import * as React from 'react';
import { Route, Routes } from 'react-router-dom';

// import './index.scss';

const CategoryUpdateView = React.lazy(() => import('./views.Update'));
const CategoryListView = React.lazy(() => import('./views.List'));

export default function CategoryRoutes() {
  const ctx = React.useContext<TSDState>(SharedDataCtx);

  if (ctx.status === 'fetching') return <Loading icon />;

  const { category } = ctx;

  return (
    <Routes>
      {category && category?.slug && <Route path=":catSlug/" element={<CategoryUpdateView />} />}
      <Route index element={<CategoryListView />} />
    </Routes>
  );
}

