import { getStaffModel, StaffService } from '@miq/staffjs';

import type { TCategory } from '../types';

class StaffCategoryService extends StaffService<TCategory> {
  publish() {
    return this.patch(`${this._path}publish/`, { is_published: true });
  }

  unpublish() {
    return this.patch(`${this._path}publish/`, { is_published: false });
  }
  updateCover(imgSlug: string) {
    return this.update({ cover: imgSlug });
  }
}

export const StaffCategory = getStaffModel('categories', StaffCategoryService);
